.head {
    background-color: #cfeaff;
    height: 50px;
    display: grid;
    grid-template-columns: 1fr 7fr;
    justify-items: right;
    align-items: center;
    position: fixed;
    width: 100%;
}

nav > a {
    padding: 10px;
}


/*1*/
.blockFirst {
    padding-top: 100px;
    padding-bottom: 50px;
    background-color: #ffdee9;
    display: grid;
    grid-template-columns: 1fr 1fr;
    min-height: 500px;
    justify-items: center;
    align-items: center;
}
.blockFirst h1 {

    background-color: #ffec74;
}

.blockFirst img {
    width: 300px;
    height: 80%;
    background-color: #98999b;
}

/*2*/
.blockTwo {
    background-color: #109a9b;
    display: grid;
    grid-template-rows: 0.5fr 1fr;
    justify-items: center;
    align-items: center;
}

.skillsHead {
    background: #ff9a7d;
    height: 50%;
    width: 80%;
    align-self: end;
}

.skillsItem {
    margin-bottom: 50px;
    width: 80%;
    background-color: #fff4b0;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 10%;
    align-self: start;


}

.skill {
    background-color: #ffcf64;
    display: grid;
    grid-template-rows: 2fr 2fr 0.35fr;
    justify-items: center;
    align-items: center;
}

.skill img {
    width: 50px;
    height: 50px;
    background-color: #ffb3ba;
}

.skill h3 {
    background-color: #edffc7;

}

.skill p {
    background-color: #cbbaff;

}
/*3*/
.blockThree {
    background-color: #9b7534;
    display: grid;
    grid-template-rows: 0.5fr 1fr;
    justify-items: center;
    align-items: center;
}

.worksHead {
    background-color: #ffd0bd;
    height: 50%;
    width: 80%;
    align-self: end;
}

.worksItems {
    margin-bottom: 50px;
    width: 80%;
    background-color: #fff4b0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10%;
    align-self: start;
}

.work {
    background-color: #b6a7ff;
    display: grid;
    grid-template-rows: 5fr 0.5fr 1.5fr;
    justify-items: center;
    align-items: center;
}

.workImg {
    grid-area: 1/1/1/1;
    background-color: #ffe1e4;
    justify-self: stretch;
    align-self: stretch;
}

.work button {
    width: 100px;
    height: 40px;
    order: 1;
    grid-area: 1/1/1/1;
}

.work h4 {
    background-color: #ffa3a7;

}

.work p {
    background-color: #ffa3a7;

}

/* b4*/
.hire {
    background-color: #9ebbff;
    display: grid;
    grid-template-rows: 1fr 1fr;
    justify-items: center;
    align-items: center;
}

.hire button {
    width: 10%;
    height: 50%;
}

/*message*/
.message {
    background-color: #c2ffe7;
    display: grid;
    grid-template-rows: 1fr 1fr 1fr 3fr 1fr;
    justify-items: center;
    align-items: center;
}

.message h3 {
    margin: 50px 10px 0px 10px;
    align-self: end;
}

.messageName {
    width: 40%;
    align-self: end;
}

.messageEmail {
    width: 40%;
}

.message textarea {
    width: 40%;
    height: 100%;
}

.message button {
    width: 10%;
    height: 50%;
}

footer {
    background-color: coral;
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
}

.follow {
    width: 60%;
    display: grid;
    grid-auto-flow: column;
    justify-items: center;
    justify-self: center;
}

.follow div {
    background-color: #b6a7ff;
    width: 50px;
    height: 50px;
}

